@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 12px;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.page__catalog {
  padding-top: 50px;
}

.header {
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  flex-basis: 40px;
  height: 40px;
}

.nav__list {
  flex-wrap: wrap;
  align-items: center;
  gap: 10px 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-weight: 500;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 16px;
  transition: transform .3s, color .3s;
}

.card__body {
  flex-direction: column;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__image {
  text-align: center;
  background: url("imac.202c52ff.jpeg") center / 160px 134px no-repeat;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__content {
  margin-bottom: 16px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card_code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 140%;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  margin-bottom: 24px;
  display: flex;
}

.card__rewies {
  color: #060b35;
  font-size: 10px;
}

.card__info {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__label {
  color: #616070;
  font-size: 12px;
  line-height: 150%;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 112%;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  box-sizing: border-box;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  transition: border .3s, background .3s, color .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  column-gap: 4px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: flex;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
